
#hello {
  margin: 30px;
}
a {
  color: #42b983;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}
.titlelink {
  color: #292b2c;
}
.card-title {
  color: #42b983;
}
.card {
  margin-top: 10px;
}
.card-footer {
  border-top: 0px;
  background-color: #fff;
  padding-bottom: 15px;
}
.card {
  border: 1px solid #292b2c;
}
.search {
  padding-bottom: 20px;
  padding-top: 0px;
}
.vaultoption {
  font-size: 26px;
}
input {
  border: 1px solid #292b2c;
}
.interaction {
  border: 1px solid #292b2c;
  padding: 10px 30px 10px 30px;
  margin: 5px;
  border-radius: 5px;
  background-color: #fff;
  color: #292b2c;
}
.interactionlink {
  border: 1px solid #292b2c;
  padding: 10px 30px 10px 30px;
  margin: 5px;
  border-radius: 5px;
  background-color: #fff;
  color: #292b2c;
}
.interactionlink:hover {
  background-color: #84FF30;
}
.add {
  border: 1px solid #292b2c;
  padding: 10px 15px 10px 15px;
  margin: 5px;
  border-radius: 5px;
  background-color: #fff;
  color: #000;
}
ul {
  margin-bottom: 5px;
  overflow: hidden;
}
li {
  line-height: 1.5em;
  float: left;
  display: inline;
  padding: 5px;
}
#double li {
  width: 50%;
}
.searchbtn {
  border: 0px solid #FF5733;
  border-radius: 5px;
  font-size: 16px;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  display: inline-block;
  color: #fff;
  background-color: #FF5733;
  margin-right: 20px;
}
.searchbtn:hover {
  border: 0px solid #FF5733;
  background-color: #FFC300;
}
.addbtn {
  border: 0px solid #FF5733;
  border-radius: 5px;
  font-size: 16px;
  padding: 5px 10px 5px 10px;
  text-decoration: none;
  display: inline-block;
  color: #000000;
  background-color: #FFC300;
  background-image: linear-gradient(to bottom, #FF5733, #FFC300);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, startColorstr=#FF5733, endColorstr=#FFC300);
  margin-right: 20px;
}
.addbtn:hover {
  border: 0px solid #FF5733;
  background-color: #FF5733;
  background-image: linear-gradient(to bottom, #FF5733, #FF5733);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, startColorstr=#FF5733, endColorstr=#FF5733);
}



